import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-pill.scss';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_DISCONNECTED} = actionTypes;
import '@servicenow/now-avatar';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import {
	schedule,
	unschedule,
	createTooltipActionHandler
} from '@servicenow/now-tooltip/src/scheduler';
import focusBehavior from '@servicenow/behavior-focus';
import {t} from 'sn-translate';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const renderIdentifier = (icon, imageSrc, size, selected) => {
	if (icon || imageSrc) {
		return (
			<span className="now-pill-identifier">
				{icon ? (
					<now-icon
						icon={
							selected
								? icon.replace('-outline', '-fill')
								: icon.replace('-fill', '-outline')
						}
						size={size}
					/>
				) : (
					<now-avatar imageSrc={imageSrc} size="sm" />
				)}
			</span>
		);
	}
};

const showTooltip = (updateState, node, tooltipText) => {
	updateState(({state}) => {
		const {ref, content, opened} = state.currentTooltip;
		return {
			path: 'currentTooltip',
			operation: 'set',
			value: {ref: node, content: tooltipText, opened: true},
			shouldUpdate: ref !== node || content !== tooltipText || !opened
		};
	});
};

const hideTooltip = (updateState) => {
	updateState(({state}) => {
		const {opened} = state.currentTooltip;
		return {
			path: 'currentTooltip',
			operation: 'set',
			value: {ref: null, content: '', opened: false},
			shouldUpdate: opened
		};
	});
};

const renderDismissButton = ({
	size,
	label,
	canDismiss,
	tooltipContent,
	dispatch,
	componentId,
	updateState
}) => {
	if (canDismiss) {
		const defaultTooltipText = t('Remove');
		const tooltipText = tooltipContent ? tooltipContent : defaultTooltipText;
		const ariaLabel = tooltipContent
			? `${tooltipContent} ${label}`
			: `${defaultTooltipText} ${label}`;

		return (
			<now-button-iconic
				className="now-pill-button-iconic"
				size={size}
				hide-padding
				bare
				variant="tertiary"
				icon="close-fill"
				id="dismiss"
				config-aria={{button: {'aria-label': ariaLabel}}}
				on-click={() => {
					dispatch(() => ({
						type: 'NOW_PILL#DISMISSED',
						payload: {}
					}));
				}}
				on-mouseenter={(e) => {
					const dismissBtn = e.target;
					schedule(componentId, () =>
						showTooltip(updateState, dismissBtn, tooltipText)
					);
				}}
				on-focusin={(e) => {
					const dismissBtn = e.target;
					schedule(componentId, () =>
						showTooltip(updateState, dismissBtn, tooltipText)
					);
				}}
				on-mouseleave={() => {
					schedule(componentId, () => hideTooltip(updateState));
				}}
				on-focusout={() => {
					schedule(componentId, () => hideTooltip(updateState));
				}}
			/>
		);
	}
};

const view = (state, {dispatch, updateState}) => {
	const {
		size,
		label,
		icon = state.properties.iconName,
		selected,
		imageSrc,
		canDismiss,
		tooltipContent
	} = state.properties;
	const {componentId} = state;

	return (
		<span
			class={{
				'now-pill': true,
				['-' + size]: size,
				'is-selected': selected
			}}>
			<button
				class={{
					'now-pill-button': true,
					'show-identifier': icon || imageSrc,
					'show-dismiss': canDismiss
				}}
				id="label"
				type="button"
				aria-pressed={selected ? 'true' : 'false'}
				on-click={() => {
					dispatch(({properties}) => ({
						type: 'NOW_PILL#SELECTED_SET',
						payload: {value: !properties.selected}
					}));
				}}>
				{renderIdentifier(icon, imageSrc, size, selected)}
				<span className="now-line-height-crop">
					<span className="now-pill-label" title={label}>
						{label}
					</span>
				</span>
			</button>
			{renderDismissButton({
				size,
				label,
				canDismiss,
				tooltipContent,
				dispatch,
				componentId,
				updateState
			})}
			<now-tooltip
				id="tooltip"
				manage-opened
				opened={state.currentTooltip.opened}
				target-ref={state.currentTooltip.ref}
				content={state.currentTooltip.content}
				delay={{show: 0, hide: 0}}
			/>
		</span>
	);
};

/**
 * Pills are UI elements you can use to sort, filter, or tag content. Pills contain
 * information such as an input, action, or attribute. Depending on the use case,
 * pills can be dynamic or predefined.
 *
 * Example use cases:
 * - Indicate selected filters in a list of filters.
 * - Display an avatar or icon to represent a selected user.
 * - Enable selection, deselection, or dismissal.
 *
 *
 * ```jsx
 * <now-pill icon="envelope-fill" can-dismiss></now-pill>
 * <now-pill image-src="http://foo.png" selected></now-pill>
 * ```
 *
 * @seismicElement now-pill
 * @summary Indicates selected values in multi-select fields.
 * @uib.label Pill
 * @uib.icon pill
 * @uib.description Indicates selected values in multi-select fields.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, size, icon, selected, imageSrc, canDismiss
 * @uib.actions NOW_PILL#SELECTED_SET, NOW_PILL#DISMISSED
 */
createEnhancedElement('now-pill', {
	properties: {
		/**
		 * Size of the pill.
		 * @type {('sm'|'md')}
		 */
		size: {
			default: 'md',
			reflect: true,
			schema: {type: 'string', enum: ['sm', 'md']}
		},
		/**
		 * Text displayed inside the pill.
		 * @type {string}
		 * @uib.description Text displayed inside the pill
		 * @uib.defaultValue Issue 42
		 * @uib.translatable true
		 */
		label: {required: true, schema: {type: 'string'}},
		/** @private */
		iconName: {},
		/**
		 * Optional icon displayed at the start of the pill.
		 * See the `now-icon` component documentation for valid inputs.
		 * The `-outline` version of this icon displays by default,
		 * even if the `-fill` version is assigned.
		 * The `-fill` version shows if a user selects the pill.
		 * @type {string}
		 * @uib.description Optional icon displayed at the start of the pill
		 * @uib.defaultValue circle-exclamation-outline
		 * @uib.fieldType icon
		 */
		icon: {schema: {type: 'string'}},
		/** @private */
		showIdentifier: {
			reflect: true,
			computed({properties}) {
				return properties.icon || properties.imageSrc ? true : false;
			},
			schema: {type: 'boolean'}
		},
		/**
		 * Color fill indicating that the pill is selected.
		 * Use `manage-selected` to override the default behavior and handle the
		 * `NOW_PILL#SELECTED_SET` action manually.
		 * @type {boolean}
		 * @uib.description Color fill indicating that the pill is selected
		 */
		selected: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * Location of an image file to display in the avatar inside of the pill.
		 * @type {string}
		 * @uib.label Image source
		 * @uib.description Location of an image file to display in the avatar inside of the pill
		 */
		imageSrc: {schema: {type: 'string'}},
		/**
		 * When true, displays a button at the end of the pill.
		 * When the user clicks the button, the pill dispatches an action.
		 * For example, clicking an (X) button removes the pill.
		 * @type {boolean}
		 * @uib.label Dismissible
		 * @uib.description Displays a button at the end of the pill. When the user clicks the button, the pill dispatches an action
		 */
		canDismiss: {default: false, reflect: true, schema: {type: 'boolean'}},
		/**
		 * Text content shown inside the tooltip for the dismiss button.
		 * If no content is provided, defaults to "Remove".
		 * @type {string}
		 * @uib.label Tooltip label for dismiss button
		 * @uib.defaultValue Enter a label
		 * @uib.description Text to display in a tooltip that describes the pill's dismiss button
		 * @uib.translatable true
		 */
		tooltipContent: {schema: {type: 'string'}}
	},
	dispatches: {
		/**
		 * Dispatched when the user selects the pill dismiss button, or if the
		 * pill is selected and the user selects the backspace or delete key.
		 * The pill does not handle this action, its parent must handle
		 * and remove the pill from its state so it is no longer rendered.
		 * @type {{}}
		 * @uib.description Dispatched when the user selects the pill dismiss button, or if the pill is selected and the user selects the backspace or delete key
		 */
		'NOW_PILL#DISMISSED': {
			schema: {
				type: 'object'
			}
		},
		/**
		 * Dispatched when the user toggles the selected status of the pill.
		 * Set the `manage-selected` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 * @uib.description Dispatched when the user toggles the selected status of the pill
		 */
		'NOW_PILL#SELECTED_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'boolean'}
				}
			}
		}
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect: ({action, host, dispatch}) => {
				const {event} = action.payload;

				if ([' ', 'Spacebar', 'Enter'].includes(event.key)) {
					action.preventDefault();
					action.stopPropagation();

					if (host.shadowRoot.activeElement.id === 'label') {
						dispatch(() => ({
							type: 'NOW_PILL#SELECTED_SET',
							payload: {value: !host.selected}
						}));
					} else if (host.shadowRoot.activeElement.id === 'dismiss') {
						dispatch(() => ({
							type: 'NOW_PILL#DISMISSED',
							payload: {}
						}));
					}
				} else if (['Backspace', 'Delete'].includes(event.key)) {
					dispatch(({properties}) => ({
						type: 'NOW_PILL#DISMISSED',
						payload: {},
						shouldDispatch: properties.canDismiss
					}));
				}
			}
		}
	],
	behaviors: [{behavior: focusBehavior}],
	styles,
	initialState: {
		currentTooltip: {ref: null, content: '', opened: false}
	},
	actionHandlers: {
		...createTooltipActionHandler(hideTooltip),
		[COMPONENT_DISCONNECTED]: ({action}) => {
			const componentId = action.meta.id;
			unschedule(componentId);
		}
	},
	view
});
