// TODO-SEISMIC-HACK: Remove if we can switch to a scheduled/cancellable action
// instead of implementing our own approach
// This scheduling is necessary because changing the target of the tooltip
// breaks its internal timing mechanism. This is necessary for WCAG which
// is rigid in its allowable timing
const TOOLTIP_DELAY = 300;
const timeoutMap = new Map();

const schedule = (key, fn, ms = TOOLTIP_DELAY) => {
	window.clearTimeout(timeoutMap.get(key));
	const newTimeout = window.setTimeout(fn, ms);
	timeoutMap.set(key, newTimeout);
};

const unschedule = (key) => {
	if (timeoutMap.has(key)) {
		window.clearTimeout(timeoutMap.get(key));
	}
};

/*
  This action handler should be used in the component managing the tooltip
  It allows for keyboard esc functionality and generally hiding the tooltip
*/
const createTooltipActionHandler = (hideTooltipCB) => {
	return {
		'NOW_TOOLTIP#OPENED_SET': ({action, properties, updateState}) => {
			const {value, target} = action.payload;
			const componentId = properties.nowId;

			if (target === 'content' && value) {
				unschedule(componentId);
			}

			if ((target === 'content' || target === 'keyboard') && !value) {
				schedule(componentId, hideTooltipCB.bind(null, updateState));
			}
		}
	};
};

export {
	TOOLTIP_DELAY,
	timeoutMap,
	schedule,
	unschedule,
	createTooltipActionHandler
};
